.billboard{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 90%;
    width: 100%;

}

.billboard-text{
    font-family: 'bluesaleregular';
    font-size: 15em;
    margin: 0px;
    padding: 0px;
}

.caption{
    font-size:3em;
    font-family: 'bluesaleregular';
}

.nav{
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    height: 10%;
    width:100%;
}

.nav-item{
    height:80%;
    width:25%;
    font-family: 'bluesaleregular';
    font-size: 5em;
    display: flex;
    justify-content: center;
    transition: 0.3s;
}

.nav-item:hover{
    color: #f27100;
    transform: translate(0,-0.5em);
}


@font-face {
    font-family: 'bluesaleregular';
    src: url('../fonts/bluesale-webfont.woff2') format('woff2'),
         url('../fonts/bluesale-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;

}
