body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  width: 100%;
  height: 100%;
}

#root{
  width:100%;
  height:100%;
}
.content{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 90%;
  width: 100%;
  font-family: 'bluesaleregular';
  font-size: 15em;
}

.page{
  height: 100%;
  width: 100%;
}

@font-face {
  font-family: 'bluesaleregular';
  src: url(/bry/static/media/bluesale-webfont.6aeb4919.woff2) format('woff2'),
       url(/bry/static/media/bluesale-webfont.9f93a557.woff) format('woff');
  font-weight: normal;
  font-style: normal;

}


.billboard{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 90%;
    width: 100%;

}

.billboard-text{
    font-family: 'bluesaleregular';
    font-size: 15em;
    margin: 0px;
    padding: 0px;
}

.caption{
    font-size:3em;
    font-family: 'bluesaleregular';
}

.nav{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    height: 10%;
    width:100%;
}

.nav-item{
    height:80%;
    width:25%;
    font-family: 'bluesaleregular';
    font-size: 5em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.nav-item:hover{
    color: #f27100;
    -webkit-transform: translate(0,-0.5em);
            transform: translate(0,-0.5em);
}


@font-face {
    font-family: 'bluesaleregular';
    src: url(/bry/static/media/bluesale-webfont.6aeb4919.woff2) format('woff2'),
         url(/bry/static/media/bluesale-webfont.9f93a557.woff) format('woff');
    font-weight: normal;
    font-style: normal;

}

